<template>
	<view :style="setBody">
		<image :style="setImg" :src="src"> </image>
		<view :style="setText">{{text}}</view>
	</view>
</template>

<script>
	export default {
		name: "icon-text",
		props: {
			//文字颜色
			color: {
				type: String,
				default: '#333'
			},
			//文字大小，单位可带可不带默认rpx
			fontSize: {
				type: String,
				default: '20rpx'
			},
			//文字
			text: {
				type: String,
				default: ''
			},
			//文字与图片距离
			margin: {
				type: String,
				default: '5rpx'
			},
			//文字与图片的排列方向
			direction: {
				type: String,
				default: 'column'
			},
			//图片宽度，只填一边另外一边默认相同
			width: {
				type: String,
				default: ''
			},
			//图片高度
			height: {
				type: String,
				default: ''
			},
			//图片角度
			radius: {
				type: String,
				default: ''
			},
			//图片源
			src: {
				type: String,
				default: ''
			}
		},
		data() {
			return {
				default_width: "50rpx"
			};
		},
		computed: {
			setBody() {
				let styles = {
					'display': 'flex',
					'flex-direction': this.direction == 'row' ? 'row' : 'column',
					'justify-content': 'center',
					'align-items': 'center',
				}
				
				let str = ''
				for (let i in styles) {
					str += i + ":" + styles[i] + ";"
				}
				
				return str
			},
			setImg() {
				let width
				let height
				let radius
				if (this.width == '' && this.height == '') {
					width = this.default_width
					height = this.default_width
				} else if (this.width == '') {
					width = this.height
					height = this.height
				} else if (this.height == '') {
					width = this.width
					height = this.width
				} else {
					width = this.width
					height = this.height
				}

				if (this.isNumber(width)) {
					width = width + "rpx"
				}

				if (this.isNumber(height)) {
					height = height + "rpx"
				}

				if (this.isNumber(this.radius)) {
					radius = this.radius + "rpx"
				} else {
					radius = this.radius
				}

				let styles = {
					'width': width,
					'height': height,
					'border-radius': radius
				}

				let str = ''
				for (let i in styles) {
					str += i + ":" + styles[i] + ";"
				}

				return str
			},
			setText() {
				let size
				let margin
				if (this.isNumber(this.fontSize)) {
					size = this.fontSize + "rpx"
				} else {
					size = this.fontSize
				}

				if (this.isNumber(this.margin)) {
					margin = this.margin + "rpx"
				} else {
					margin = this.margin
				}

				if (this.direction == 'row') {
					margin = "0 0 0 " + margin
				} else {
					margin = margin + " 0 0 0"
				}

				let styles = {
					'color': this.color,
					'font-size': size,
					'margin': margin
				}
				
				let str = ''
				for (let i in styles) {
					str += i + ":" + styles[i] + ";"
				}
				
				return str
			}
		},
		methods: {
			isNumber(val) {
				var regPos = /^\d+(\.\d+)?$/; //非负浮点数
				var regNeg = /^(-(([0-9]+\.[0-9]*[1-9][0-9]*)|([0-9]*[1-9][0-9]*\.[0-9]+)|([0-9]*[1-9][0-9]*)))$/; //负浮点数
				if (regPos.test(val) || regNeg.test(val)) {
					return true;
				} else {
					return false;
				}
			}
		}
	}
</script>

<style>

</style>
